body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #b5b5b5;
}

.App {
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  padding: 32px;
  text-align: center;
  width: 400px;
}

h1 {
  margin-top: 0;
}

button {
  border: none;
  border-radius: 15%;
  color: #fefefe;
  background-color: #989696;
  cursor: pointer;
  font-size: 24px;
  height: 50px;
  margin: 8px;
  width: 50px;
}

button:hover {
  opacity: 0.8;
}

button:active {
  transform: translateY(2px);
}

#break-label,
#session-label,
#timer-label {
  font-size: 24px;
  margin: 16px 0 8px;
}

#break-length,
#session-length,
#time-left {
  font-size: 48px;
  margin: 0;
}

#reset {
  width: 100px;
  background-color: #c0392b;
}

#start_stop {
  width: 100px;
  background-color: #2ecc71;
}
